import { Unit, Enemy } from "./InterfacesBaseClasses/Enemy";
import { Player } from "./InterfacesBaseClasses/Player";
import { StageLevel } from "./InterfacesBaseClasses/StageLevel";
export var thePlayer = new Player(1);
export var theStage = new StageLevel(1);
console.log(Enemy);
console.log(Unit);
console.log(Player);
var EnemyArrCounter = 1;
var StageOneEnemyArr = [new Enemy(5, 5)];
var StageTwoEnemyArr = [new Enemy(5, 5)];
var StageThreeEnemyArr = [new Enemy(5, 5)];
var StageFourEnemyArr = [new Enemy(5, 5)];
var StageFiveEnemyArr = [new Enemy(5, 5)];
var EnemyArr = [StageOneEnemyArr, StageTwoEnemyArr, StageThreeEnemyArr, StageFourEnemyArr, StageFiveEnemyArr];
var CurrentEnemyArr = StageOneEnemyArr;
var RangeOneUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var RangeTwoUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var RangeThreeUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var RangeFourUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var RangeFiveUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var RangeSixUnitArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var HeroArr = [new Unit(0, "abc", "abc", 123, 123, 123)];
var UnitArr = [RangeOneUnitArr, RangeTwoUnitArr, RangeThreeUnitArr, RangeFourUnitArr, RangeFiveUnitArr, RangeSixUnitArr, HeroArr];
var CurrentUnit;
var counter = 0;
setInterval(function () {
    counter++;
    MainGameCycle(counter);
}, 50);
function MainGameCycle(currentTime) {
    UnitArr.forEach(function (s) { return s.forEach(function (u) { return u.UpdateFeedback(currentTime); }); });
    CurrentEnemyArr[0].UpdateFeedback(currentTime);
}
export function GetCurrentEnemy() {
    return CurrentEnemyArr[0];
}
export function GetCurrentUnit() {
    return CurrentUnit;
}
export function RemoveByDeath(type) {
    if (type == "Unit") {
        var isEmpty = true;
        for (var i = 0; i < UnitArr.length && isEmpty; i++) {
            for (var j = 0; j < UnitArr[i].length && isEmpty; j++) {
                if (UnitArr[i][j].Count > 0) {
                    isEmpty = false;
                    UnitArr[i][j].Birth();
                    CurrentUnit = UnitArr[i][j];
                }
            }
        }
        if (isEmpty) {
            thePlayer.Birth();
        }
    }
    else if (type == "Enemy") {
        CurrentEnemyArr.slice(1);
        if (!CurrentEnemyArr.length) {
            CurrentEnemyArr = EnemyArr[EnemyArrCounter % 5];
            EnemyArrCounter++;
        }
        CurrentEnemyArr[0].Birth();
    }
}
export function AddToArray(object, type, category) {
    if (type == "Unit") {
        UnitArr[category].push(object);
    }
}
