import { thePlayer, GetCurrentEnemy, RemoveByDeath, theStage, GetCurrentUnit } from "../Database";
import $ from "jquery";
var Unit = (function () {
    function Unit(id, image, name, baseHP, baseDamage, range) {
        this.id = id;
        this.image = image;
        this.name = name;
        this.baseHP = baseHP;
        this.maxHP = this.baseHP * thePlayer.ArmyVitality;
        this.currentHP = this.MaxHP;
        this.baseDamage = baseDamage;
        this.currentDamage = this.baseDamage * thePlayer.ArmyVitality;
        this.range = range;
        this.count = 0;
        this.isUnlocked = false;
    }
    Unit.prototype.UpdateFeedback = function (counter) {
        if (counter % 20) {
            this.Hurt();
            this.Regenerate(10 * thePlayer.ArmyVitality);
        }
    };
    Object.defineProperty(Unit.prototype, "MaxHP", {
        get: function () {
            this.maxHP = this.baseHP * thePlayer.ArmyVitality;
            return this.maxHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Unit.prototype, "CurrentHP", {
        get: function () {
            return this.currentHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Unit.prototype, "CurrentDamage", {
        get: function () {
            this.currentDamage = this.baseDamage * thePlayer.ArmyVitality * this.Count;
            return this.currentDamage;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Unit.prototype, "Count", {
        get: function () {
            return this.count;
        },
        enumerable: true,
        configurable: true
    });
    Unit.prototype.ReceiveDamage = function (damage) {
        this.currentHP -= damage;
        this.currentHP = Math.max(this.currentHP, 0);
        adjustBarAnimation("fighter-hp", (this.CurrentHP / this.MaxHP));
        if (this.currentHP == 0) {
            this.Die();
            this.Unexist(1);
        }
    };
    Unit.prototype.Unlocked = function () {
        alert("You have unlocked " + this.name);
        this.isUnlocked = true;
    };
    Unit.prototype.Exist = function (count) {
        if (!this.isUnlocked) {
            this.Unlocked();
        }
        ;
        this.count += count;
    };
    Unit.prototype.Unexist = function (count) {
        this.count -= count;
        RemoveByDeath("Unit");
    };
    Unit.prototype.Birth = function () {
        adjustBarAnimation("fighter-hp", this.MaxHP);
    };
    Unit.prototype.Die = function () {
    };
    Unit.prototype.Hurt = function () {
        GetCurrentEnemy().ReceiveDamage(this.CurrentDamage);
    };
    Unit.prototype.Regenerate = function (health) {
        this.currentHP += health;
        this.currentHP = Math.min(this.MaxHP, this.CurrentHP);
        adjustBarAnimation("fighter-hp", (this.CurrentHP / this.MaxHP));
    };
    return Unit;
}());
var Enemy = (function () {
    function Enemy(baseHP, baseDamage) {
        this.baseHP = baseHP;
        this.maxHP = this.baseHP * theStage.Level;
        this.currentHP = this.MaxHP;
        this.baseDamage = baseDamage;
        this.currentDamage = this.baseDamage * theStage.Level;
    }
    Enemy.prototype.UpdateFeedback = function (counter) {
        if (counter % 20) {
            this.Hurt();
            this.Regenerate(10 * theStage.Level);
        }
    };
    Object.defineProperty(Enemy.prototype, "MaxHP", {
        get: function () {
            this.maxHP = this.baseHP * theStage.Level;
            return this.maxHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Enemy.prototype, "CurrentHP", {
        get: function () {
            return this.currentHP;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(Enemy.prototype, "CurrentDamage", {
        get: function () {
            this.currentDamage = this.baseDamage * theStage.Level;
            return this.currentDamage;
        },
        enumerable: true,
        configurable: true
    });
    Enemy.prototype.ReceiveDamage = function (damage) {
        this.currentHP -= damage;
        this.currentHP = Math.max(this.currentHP, 0);
        adjustBarAnimation("monster-hp", (this.CurrentHP / this.MaxHP));
        if (this.currentHP == 0) {
            this.Die();
        }
    };
    Enemy.prototype.Birth = function () {
        adjustBarAnimation("monster-hp", this.MaxHP);
    };
    Enemy.prototype.Die = function () {
        RemoveByDeath("Enemy");
    };
    Enemy.prototype.Hurt = function () {
        GetCurrentUnit().ReceiveDamage(this.CurrentDamage);
    };
    Enemy.prototype.Regenerate = function (health) {
        this.currentHP += health;
        this.currentHP = Math.min(this.CurrentHP, this.MaxHP);
        adjustBarAnimation("monster-hp", (this.CurrentHP / this.MaxHP));
    };
    return Enemy;
}());
function adjustBarAnimation(type, percentage) {
    $("#" + type + "-bar").animate({ "width": ("" + percentage.toString() + "%") }, 200);
    if (percentage == 100) {
        $("#" + type + "-bar").animate({ "width": ("0%") }, 100);
    }
}
export { Unit, Enemy };
